import { Component, HostListener, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router, RouterModule } from '@angular/router';

import { RefreshToken } from '@biz-nest/auth-client';
import {
  AppNameService,
  EventbusService, ListComposeAdminPanel,
  MainBarTitleService,
  scrollToTop, WindowService
} from '@front-shared-lib';
import { navByBus } from '@main-shared-lib';
import { ToastrService } from 'ngx-toastr';
import { AdminDateFilterFieldComponent } from '../../misc/admin-date-filter-field/admin-date-filter-field.component';
import { AdminModalComponent } from '../../misc/admin-modal/admin-modal.component';
import { AdminPageContentComponent } from '../../misc/admin-page-content/admin-page-content.component';
import { AdminSearchFieldComponent } from '../../misc/admin-search-field/admin-search-field.component';
import { AdminTableComponent } from '../../misc/admin-table/admin-table.component';
import { AdminTopBarComponent } from '../../misc/admin-top-bar/admin-top-bar.component';

@Component({
  selector: 'app-refresh-token-list',
  standalone: true,
  imports: [
    RouterModule,
    AdminModalComponent,
    AdminTableComponent,
    AdminPageContentComponent,
    AdminSearchFieldComponent,
    AdminDateFilterFieldComponent,
    AdminTopBarComponent
  ],
  templateUrl: './refresh-token-list.component.html',
  styleUrl: './refresh-token-list.component.scss'
})
export class RefreshTokenListComponent
implements OnInit, OnDestroy {
  posting = false;
  cleanup?: () => void;
  listCompose = new ListComposeAdminPanel<RefreshToken>(
    RefreshToken,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  actions = [
    {
      label: 'View User',
      icon: 'person',
      event: (refreshToken: RefreshToken) => this.viewUser(refreshToken)
    },
    {
      label: 'Revoke',
      icon: 'edit',
      event: (refreshToken: RefreshToken) => this.revokeRefreshToken(refreshToken)
    },
    {
      label: 'Delete',
      icon: 'delete',
      event: (refreshToken: RefreshToken) => this.listCompose
        .openDeleteOneModal(
          refreshToken._id,
          'Delete RefreshToken',
          'Are You Sure You Want To Delete This RefreshToken?'
        )
    }
  ];

  isFromRevoke = false;
  refreshToken?: RefreshToken;

  constructor(
    private windowService: WindowService,
    private toastr: ToastrService,
    private router: Router,
    private eventbus: EventbusService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) { }

  @HostListener('document:click', ['$event'])
  onDocumentClick() {
    this.listCompose.currentActivePopupId = '';
  }

  ngOnInit(): void {
    this.listCompose.getDataArr();
    this.mainBarTitle.title = 'Refresh Token List';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: `Efficiently manage and comprehensively monitor your refresh tokens,
      empowering you to maintain financial clarity and make informed decisions
      regarding your fiscal responsibilities and opportunities for savings.`
    });

    scrollToTop(this.windowService);
  }


  revokeRefreshToken(refreshToken: RefreshToken) {
    this.isFromRevoke = true;
    this.listCompose.showModal = true;
    this.listCompose.modalTitle = 'Revoke Refresh Token';
    this.listCompose.modalMsg = 'Are you sure you want to revoke this refresh token?';
    this.listCompose.modalCancelButton = true;
    this.listCompose.modalCancelText = 'Cancel';
    this.listCompose.modalConfirmText = 'Revoke';
    this.listCompose.modalConfirmButton = true;
    this.refreshToken = refreshToken;
  }

  onModalDeleteConfirm(event: boolean) {
    if (this.isFromRevoke && this.refreshToken) {
      this.refreshToken.revoke();
    } else {
      this.listCompose.onModalDeleteConfirm(event);
    }

    this.listCompose.toggleModal();
  }

  viewUser(refreshToken: RefreshToken) {
    this.router.navigate(['/manage-user/' + refreshToken.user]);
  }

  ngOnDestroy(): void {
    this.listCompose.cleanup?.();
  }
}
